html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
}

body {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#APP,
#page,
#content,
.ast-container,
#primary,
#main,
article,
.entry-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

#Rain {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
#Rain .videobg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
#Rain canvas#container {
  position: absolute;
  top: 0;
  left: 0;
}